"use client"

import styled from "styled-components";
import { useParams } from "next/navigation";
import TitleSection from "@/components/newsDetail/TitleSection";
import { useEffect, useState } from "react";
import { INewsDetail } from "@/models/newsDetail";
import { GET } from "@/app/api/news/[slug]/route";

function NewsletterDetailPage() {
  const [ newsInfo, setNewsInfo ] = useState<INewsDetail | null>(null)
  const params = useParams();
  const slug = params.slug;

  useEffect(() => {
    console.log("Params:", typeof slug); // 값 확인
    async function fetchNewsDetail() {
      if (slug) {
        try {
          const res = await GET(`/api/news/${slug}`);
          if (!res.ok) {
            throw new Error("No such news found");
          }
          const data = await res.json();
          setNewsInfo(data);
        } catch (err) {
          console.error(err);
        }
      }
    }

    fetchNewsDetail();
  }, [params]);

  return (
    <NewsletterDetailPageStyled>
      <TitleSection category={newsInfo?.categoryId} title={newsInfo?.title} date={newsInfo?.createdAt}/>
    </NewsletterDetailPageStyled>
  );
}

const NewsletterDetailPageStyled = styled.div`

`;

export default NewsletterDetailPage;